import React from "react";
import { MdCheckCircle } from "react-icons/md";
import { connect } from "react-redux";
import {
  AssignCandidateInterface,
  Auth,
  Basic,
  FC_GetAssignAssets,
  FT_GetAssignAssets,
  FC_AssignCandidate,
  FC_GetSchoolPositions,
  FC_GetDistrictList,
  DistrictList,
  FT_GetSchoolsByDistrictRedux,
  FC_GetSchoolsByDistrictRedux,
} from "../../actions";
import Alert from "../../components/Alert/Alert";
import DashboardContainer from "../../components/DashboardContainer/DashboardContainer";
import InputSelect from "../../components/Inputs/InputSelect";
import TextInput from "../../components/Inputs/TextInput";
import { SchoolPositionsCombination } from "../../containers/Head-Teacher/Request/HeadTeacherRequestToDee";
import { StoreState } from "../../reducers";
import Button from "../Buttons/Button";
import Heading from "../Heading/Heading";
import Loading from "../Loading/Loading";
import SelectCustom from "../SelectCustom/SelectCustom";
import { containsNumbersOnly } from "../../utils/functions";

export interface formAccess {
  role_id: boolean;
  country_id: boolean;
  district_code: boolean;
  sector_code: boolean;
  school_code: boolean;
  qualification_id: boolean;
  academic_year_id: boolean;
  stakeholder_id: boolean;
  start_date_in_the_school: boolean;
  position_id: boolean;
  first_name: boolean;
  last_name: boolean;
  full_name: boolean;
  middle_name: boolean;
  nid: boolean;
  phone_numbers: boolean;
  passport: boolean;
}

// props for the component
interface AppProps {
  auth: Auth;
  basic: Basic;
  formAccess: formAccess;
  required: formAccess;
  FC_GetAssignAssets: FT_GetAssignAssets;
  success: (data: AssignCandidateInterface) => void;
  FC_GetSchoolsByDistrictRedux: FT_GetSchoolsByDistrictRedux;
  district_id: string;
  school?: { school_id: string; school_name: string };
  user_id: string;
  assignType: "REGISTER" | "ASSIGN" | "UPDATE_POSITION";
  position_code?: string;
  qualification_id?: string;
  button_title?: string;
  only_post_for_school?: boolean;
  exclude_roles?: string[];
  current_position?: string | undefined | null;
  select_district_active?: boolean;
  no_position?: boolean;
  nid_value?: string;
  passport_value?: string;
}

interface AppState {
  loading_assets: boolean;
  loading: boolean;
  select_school_open: boolean;
  select_sector_open: boolean;
  select_role_open: boolean;
  select_district_open: boolean;
  select_position_open: boolean;
  error: {
    target:
      | "main-success"
      | "main-error"
      | null
      | "role_id"
      | "country_id"
      | "district_code"
      | "sector_code"
      | "school_code"
      | "qualification_id"
      | "academic_year_id"
      | "stakeholder_id"
      | "start_date_in_the_school"
      | "district_list_select"
      | "position_id"
      | "first_name"
      | "last_name"
      | "full_name"
      | "middle_name"
      | "nid"
      | "passport"
      | "phone_numbers"
      | "main-success-register";
    msg: string;
  };
  role_id: string;
  country_id: string;
  district_code: string;
  sector_code: string;
  school_code: string;
  qualification_id: string;
  academic_year_id: string;
  stakeholder_id: string;
  start_date_in_the_school: string;
  position_id: string;

  //
  first_name: string;
  last_name: string;
  full_name: string;
  middle_name: string;
  nid: string;
  passport: string;
  phone_numbers: string;
  loading_positions_schools: boolean;
  school_posts: SchoolPositionsCombination | null;
  assignType: "REGISTER" | "ASSIGN" | "UPDATE_POSITION";
  loading_districts: boolean;
  district_list: DistrictList[];
  district_name: string;
  loading_assigning_data: boolean;
}

class _AssignStuffComponent extends React.Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      loading_assets: true,
      loading: false,
      select_school_open: false,
      select_sector_open: false,
      select_role_open: false,
      select_district_open: false,
      select_position_open: false,
      error: {
        target: null,
        msg: "",
      },
      district_code: this.props.district_id,
      sector_code: "",
      school_code: this.props.school ? this.props.school.school_id : "",
      academic_year_id: "",
      stakeholder_id: "",
      role_id: "",
      country_id: "",
      start_date_in_the_school: "",
      position_id: this.props.position_code ? this.props.position_code : "",
      qualification_id:
        this.props.qualification_id === undefined
          ? ""
          : this.props.qualification_id,

      // new stuff
      first_name: "",
      last_name: "",
      full_name: "",
      middle_name: "",
      nid: this.props.nid_value ? this.props.nid_value : "",
      passport: this.props.passport_value ? this.props.passport_value : "",
      phone_numbers: "",
      // For position of school
      loading_positions_schools: false,
      school_posts: null,
      assignType: this.props.assignType,
      loading_districts: false,
      district_list: [],
      district_name: this.props.auth.school?.school_code
        ? this.props.auth.school.school_code.slice(0, 2)
        : "",
      loading_assigning_data: false,
    };
  }

  //** This is  */
  getDistrictAssets(district_id: string) {
    this.setState({
      loading_assigning_data: true,
    });
    this.props.FC_GetAssignAssets(district_id, (status, msg) => {
      if (status === true) {
        this.setState({
          error: {
            target: null,
            msg: "",
          },
          loading_assigning_data: false,
          school_code: "",
          select_school_open: true,
        });
      } else {
        this.setState({
          error: {
            target: "main-error",
            msg: msg,
          },
          loading_assigning_data: false,
          school_code: "",
          select_school_open: true,
        });
      }
    });
  }

  componentDidMount() {
    console.log(
      this.props.auth.school
        ? this.props.auth.school.school_code.slice(0, 2)
        : ""
    );

    let district_id: string = "";
    if (this.props.auth.district)
      district_id = this.props.auth.district.district_code;
    if (this.props.auth.district === null && this.props.auth.school)
      district_id = this.props.auth.school.school_code.slice(0, 2);
    this.setState({
      district_code: district_id,
    });

    // if (
    //   this.props.basic.assign_teacher_assets.loading === true ||
    //   this.props.basic.assign_teacher_assets.data === null
    // ) {
    this.getDistrictAssets(district_id);
    // }

    // Get the positions for the current selected school
    if (
      this.props.school !== undefined &&
      this.props.only_post_for_school !== undefined
    ) {
      this.loadSchoolPosts(this.props.school?.school_id);
    }

    // Get all the districts
    if (
      this.props.select_district_active &&
      this.props.select_district_active === true
    ) {
      this.setState({
        loading_districts: true,
        // district_list: DistrictList[]:
      });
      FC_GetDistrictList((status, data, msg) => {
        if (status === true) {
          this.setState({
            loading_districts: false,
            district_list: data,
          });
        } else {
          this.setState({
            error: {
              target: "district_list_select",
              msg: msg,
            },
            loading_districts: false,
          });
        }
      });
    }
  }

  loadSchoolPosts(school_id: string) {
    if (this.props.only_post_for_school !== undefined) {
      // Get school posts
      if (this.props.basic.academic_calender?.academic_year_id) {
        this.setState({
          loading_positions_schools: true,
        });
        FC_GetSchoolPositions(
          school_id,
          this.props.basic.academic_calender?.academic_year_id,
          (status, data, msg) => {
            console.log({ status, data, msg });

            if (status === true) {
              this.setState({
                loading_positions_schools: false,
                school_posts: data,
              });
            } else {
              this.setState({
                loading_positions_schools: false,
                school_posts: null,
                error: {
                  target: "main-error",
                  msg: "Failed to load positions for this schools",
                },
              });
            }
            // console.log({
            //   status,
            //   data,
            //   msg,
            // });
          }
        );
      }
    }
  }

  componentDidUpdate(prevProps: AppProps) {
    if (
      prevProps.school?.school_id !== this.props.school?.school_id ||
      prevProps.district_id !== this.props.district_id
    ) {
      if (this.props.school !== undefined) {
        this.setState({
          school_code: this.props.school.school_id,
          position_id: "",
          select_school_open: false,
        });
        this.loadSchoolPosts(this.props.school.school_id);
      } else {
        this.setState({
          school_code: "",
          position_id: "",
          select_position_open: false,
          select_school_open: true,
        });
      }
    }
  }

  submitLogin = (e: any) => {
    e.preventDefault();

    if (
      this.props.current_position &&
      this.props.school &&
      this.props.current_position === this.state.position_id &&
      this.state.school_code === this.props.school?.school_id
    )
      return this.setState({
        error: {
          target: "position_id",
          msg: "The selected position is the same as the current position of this user on this school",
        },
      });

    if (this.props.assignType === "REGISTER") {
      if (this.state.nid !== "") {
        if (this.state.nid.length !== 16)
          return this.setState({
            error: {
              target: "nid",
              msg: "NID must be 16 digits.",
            },
          });

        if (!containsNumbersOnly(this.state.nid))
          return this.setState({
            error: {
              target: "nid",
              msg: "NID must be digits only.",
            },
          });
      }

      if (
        (this.state.nid === "" || this.state.nid === null) &&
        (this.state.passport === "" || this.state.passport === null)
      ) {
        if (this.state.nid.length !== 16)
          return this.setState({
            error: {
              target: "nid",
              msg: "Please provide an nid or passport",
            },
          });
      }

      if (
        this.props.required.phone_numbers === true &&
        this.state.phone_numbers.length <= 3
      )
        return this.setState({
          error: {
            target: "phone_numbers",
            msg: "Required",
          },
        });

      if (
        this.props.required.first_name === true &&
        this.state.first_name.length <= 3
      )
        return this.setState({
          error: {
            target: "first_name",
            msg: "Required",
          },
        });

      if (
        this.props.required.middle_name === true &&
        this.state.middle_name.length <= 3
      )
        return this.setState({
          error: {
            target: "middle_name",
            msg: "Required",
          },
        });

      if (
        this.props.required.last_name === true &&
        this.state.last_name.length <= 3
      )
        return this.setState({
          error: {
            target: "last_name",
            msg: "Required",
          },
        });

      if (
        this.props.required.full_name === true &&
        this.state.full_name.length <= 3
      )
        return this.setState({
          error: {
            target: "full_name",
            msg: "Required",
          },
        });
    }

    if (
      this.props.required.school_code === true &&
      this.state.school_code === ""
    )
      return this.setState({
        error: {
          target: "school_code",
          msg: "Required",
        },
      });

    if (this.props.required.role_id === true && this.state.role_id === "")
      return this.setState({
        error: {
          target: "role_id",
          msg: "Required",
        },
      });

    if (this.props.basic.academic_calender === null)
      return this.setState({
        error: {
          target: "main-error",
          msg: "You have no academic selected, Please refresh the page.",
        },
      });

    if (
      this.props.required.position_id === true &&
      this.state.position_id === ""
    )
      return this.setState({
        error: {
          target: "position_id",
          msg: "Required",
        },
      });

    if (
      this.props.required.start_date_in_the_school === true &&
      this.state.start_date_in_the_school === ""
    )
      return this.setState({
        error: {
          target: "start_date_in_the_school",
          msg: "Required",
        },
      });

    // check if there is a selected district
    if (
      this.props.select_district_active &&
      this.props.select_district_active === true &&
      this.state.district_code.length <= 0 &&
      this.props.auth.school === null
    )
      return this.setState({
        error: {
          target: "district_list_select",
          msg: "Required",
        },
      });

    let AssignType = this.props.assignType;

    if (this.props.assignType !== "REGISTER") {
      if (
        this.props.school?.school_id &&
        this.props.school?.school_id === this.state.school_code
      )
        AssignType = "UPDATE_POSITION";
    }

    let SendData = {
      role_id: this.state.role_id,
      country_id: this.state.country_id,
      district_code: this.state.district_code,
      sector_code: this.state.sector_code,
      school_code: this.state.school_code,
      qualification_id: this.state.qualification_id,
      academic_year_id: this.props.basic.academic_calender.academic_year_id,
      stakeholder_id: this.state.stakeholder_id,
      start_date_in_the_school: "",
      hired_date: this.state.start_date_in_the_school,
      position_code: this.state.position_id,
      user_id: this.props.user_id,
      first_name: this.state.first_name,
      last_name: this.state.last_name,
      full_name: this.state.full_name,
      middle_name: this.state.middle_name,
      nid: this.state.nid,
      phone_numbers: this.state.phone_numbers,
      key: AssignType,
    } as AssignCandidateInterface;

    //* Save data
    this.setState({
      loading: true,
    });
    FC_AssignCandidate(SendData, (status, msg) => {
      if (status === true) {
        this.setState({
          loading: false,
          error: {
            target: "main-success-register",
            msg: "Staff was added successfully",
          },
        });
        this.props.success(SendData);
      } else {
        this.setState({
          loading: false,
          error: {
            target: "main-error",
            msg: msg,
          },
        });
      }

      setTimeout(() => {
        this.setState({
          error: {
            target: null,
            msg: "",
          },
        });
      }, 3000);
    });
  };

  render() {
    if (
      this.props.basic.assign_teacher_assets.loading === true ||
      this.props.basic.assign_teacher_assets.data === null
    )
      return (
        <DashboardContainer>
          <div className="py-6 mx-auto text-center">
            <Loading />
          </div>
        </DashboardContainer>
      );

    let assign_teacher_assets = this.props.basic.assign_teacher_assets.data;
    let selected_school_name_exist =
      this.props.basic.assign_teacher_assets.data.schools.find(
        (itm) => itm.school_code === this.state.school_code
      );
    let selected_school_name =
      this.props.auth.school !== null
        ? `${this.props.auth.school.school_code} - ${this.props.auth.school.school_name}  `
        : selected_school_name_exist !== undefined
        ? `${selected_school_name_exist.school_code} - ${selected_school_name_exist.school_name} `
        : "Select a school";

    // Sector
    let sector_exist = assign_teacher_assets.sectors.find(
      (itm) => itm.sector_code === this.state.sector_code
    );
    let sector_name =
      sector_exist !== undefined ? sector_exist.sector_name : "";

    // Role
    let role_exist = assign_teacher_assets.roles.find(
      (itm) => itm.role_id === this.state.role_id
    );
    let role_name = role_exist !== undefined ? role_exist.role : "";

    let positions_exist = assign_teacher_assets.positions.find(
      (itm) => itm.position_code === this.state.position_id
    );
    let positions_name =
      positions_exist !== undefined
        ? `${positions_exist.position_code} - ${positions_exist.position_name} - ${positions_exist.qualification_name}`
        : "";

    // let foundSchool = this.props.basic.assign_teacher_assets.data.schools.find(
    //   (sk) => sk.school_code === this.props.auth.school?.school_code
    // );
    let SchoolsAvailablesList =
      this.props.auth.school === null
        ? this.props.basic.assign_teacher_assets.data.schools.map((itm) => ({
            ...itm,
            school_name: `${itm.school_code} - ${itm.school_name}  ${itm.school_category}`,
          }))
        : [
            {
              ...this.props.auth.school,
              school_name: `${this.props.auth.school.school_code} - ${this.props.auth.school.school_name}  `,
            },
          ];

    if (this.state.error.target === "main-success-register")
      return (
        <DashboardContainer className="bg-green-100 py-10 ">
          <div className="text-center">
            <h1 className="text-8xl text-green-600 inline-block">
              <MdCheckCircle />
            </h1>
            <h2 className="text-2xl font-bold text-green-900">
              Candidate has been added to the system
            </h2>
            <p className="pb-3 text-lg">{this.state.error.msg}</p>
            <p className="pb-4 opacity-50">
              The user <b>{this.state.nid}</b> is now registered
            </p>

            <Button
              theme="success"
              onClick={() =>
                this.setState({ error: { target: null, msg: "" } })
              }
            >
              Add another teacher
            </Button>
          </div>
        </DashboardContainer>
      );

    return (
      <>
        {this.state.error.target === "main-error" && (
          <Alert theme="danger" fixed>
            {this.state.error.msg}
          </Alert>
        )}

        {this.state.error.target === "main-success" && (
          <Alert theme="success" fixed>
            {this.state.error.msg}
          </Alert>
        )}

        <DashboardContainer className="animate__animated animate__zoomIn animate__faster">
          <form onSubmit={this.submitLogin}>
            <div className="p-2 flex flex-wrap">
              {this.props.assignType === "REGISTER" && (
                <>
                  <div className="border-b-2 w-full my-2">
                    <Heading size="sm">User information</Heading>
                  </div>

                  {this.props.formAccess.nid && (
                    <div className="w-full lg:w-1/2 py-1 px-3">
                      <TextInput
                        disabled={this.state.loading}
                        label="NID/Passport"
                        name="nid"
                        type="text"
                        max={16}
                        onChange={(e) => {
                          // if (e.target.value.length <= 16)
                          this.setState({
                            nid: e.target.value,
                            error: { target: null, msg: "" },
                          });
                        }}
                        value={this.state.nid}
                        error={
                          this.state.error.target === "nid"
                            ? this.state.error.msg
                            : ""
                        }
                        placeholder={"National ID"}
                        className="p-2"
                      />
                    </div>
                  )}

                  {this.props.formAccess.passport && (
                    <div className="w-full lg:w-1/2 py-1 px-3">
                      <TextInput
                        disabled={this.state.loading}
                        label="Passport"
                        name="passport"
                        type="text"
                        max={16}
                        onChange={(e) => {
                          // if (e.target.value.length <= 16)
                          this.setState({
                            passport: e.target.value,
                            error: { target: null, msg: "" },
                          });
                        }}
                        value={this.state.passport}
                        error={
                          this.state.error.target === "passport"
                            ? this.state.error.msg
                            : ""
                        }
                        placeholder={"Passport"}
                        className="p-2"
                      />
                    </div>
                  )}

                  {this.props.formAccess.phone_numbers && (
                    <div className="w-full lg:w-1/2 py-1 px-3">
                      <TextInput
                        disabled={this.state.loading}
                        label="Phone number"
                        name="phone_numbers"
                        type="number"
                        onChange={(e) => {
                          if (e.target.value.length <= 10)
                            this.setState({
                              phone_numbers: e.target.value,
                              error: { target: null, msg: "" },
                            });
                        }}
                        value={this.state.phone_numbers}
                        error={
                          this.state.error.target === "phone_numbers"
                            ? this.state.error.msg
                            : ""
                        }
                        placeholder={"0700000000"}
                        className="p-2"
                      />
                    </div>
                  )}
                  {this.props.formAccess.first_name && (
                    <div className="w-full lg:w-1/3 py-1 px-3">
                      <TextInput
                        disabled={this.state.loading}
                        label="First name (Irinyarwanda)"
                        name="first_name"
                        onChange={(e) =>
                          this.setState({
                            first_name: e.target.value,
                            full_name: `${
                              e.target.value !== null ? e.target.value : ""
                            } ${
                              this.state.middle_name !== null
                                ? this.state.middle_name
                                : ""
                            } ${
                              this.state.last_name !== null
                                ? this.state.last_name
                                : ""
                            }`,
                            error: { target: null, msg: "" },
                          })
                        }
                        value={this.state.first_name}
                        error={
                          this.state.error.target === "first_name"
                            ? this.state.error.msg
                            : ""
                        }
                        placeholder={"Ex: KAMANA"}
                        className="p-2"
                      />
                    </div>
                  )}
                  {this.props.formAccess.middle_name && (
                    <div className="w-full lg:w-1/3 py-1 px-3">
                      <TextInput
                        disabled={this.state.loading}
                        label="Middle name"
                        name="middle_name"
                        onChange={(e) =>
                          this.setState({
                            middle_name: e.target.value,
                            full_name: `${
                              this.state.first_name !== null
                                ? this.state.first_name
                                : ""
                            } ${
                              e.target.value !== null ? e.target.value : ""
                            } ${
                              this.state.last_name !== null
                                ? this.state.last_name
                                : ""
                            }`,
                            error: { target: null, msg: "" },
                          })
                        }
                        value={this.state.middle_name}
                        error={
                          this.state.error.target === "middle_name"
                            ? this.state.error.msg
                            : ""
                        }
                        placeholder={"Middle name"}
                        className="p-2"
                      />
                    </div>
                  )}
                  {this.props.formAccess.last_name && (
                    <div className="w-full lg:w-1/3 py-1 px-3">
                      <TextInput
                        disabled={this.state.loading}
                        label="Last name"
                        name="last_name"
                        onChange={(e) =>
                          this.setState({
                            last_name: e.target.value,
                            full_name: `${
                              this.state.first_name !== null
                                ? this.state.first_name
                                : ""
                            } ${
                              this.state.middle_name !== null
                                ? this.state.middle_name
                                : ""
                            } ${e.target.value !== null ? e.target.value : ""}`,
                            error: { target: null, msg: "" },
                          })
                        }
                        value={this.state.last_name}
                        error={
                          this.state.error.target === "last_name"
                            ? this.state.error.msg
                            : ""
                        }
                        placeholder={"Ex: Emmanuel"}
                        className="p-2"
                      />
                    </div>
                  )}
                  {this.props.formAccess.full_name && (
                    <div className="w-full  py-1 px-3">
                      <TextInput
                        disabled={true}
                        label="Full name"
                        name="full_name"
                        onChange={(e) =>
                          this.setState({
                            full_name: e.target.value,
                            error: { target: null, msg: "" },
                          })
                        }
                        value={this.state.full_name}
                        error={
                          this.state.error.target === "full_name"
                            ? this.state.error.msg
                            : ""
                        }
                        placeholder={"Full name"}
                        className="p-2"
                      />
                    </div>
                  )}
                </>
              )}

              <div className="border-b-2 w-full my-2">
                <Heading size="sm">Academic info</Heading>
              </div>

              <div className="w-full lg:flex py-1 px-3 bg-gray-100 rounded">
                {this.props.auth.school !== null
                  ? null
                  : this.props.select_district_active &&
                    this.props.select_district_active === true && (
                      <>
                        {this.state.select_district_open === true ? (
                          <div className="w-full lg:w-1/2 py-1 px-3">
                            <SelectCustom
                              click={(data: any) => {
                                this.setState({
                                  district_code: data.district_code,
                                  district_name: data.district_name,
                                  select_district_open: false,
                                  error: {
                                    target: null,
                                    msg: "",
                                  },
                                });
                                // Load schools
                                this.setState({ loading_assigning_data: true });
                                this.props.FC_GetSchoolsByDistrictRedux(
                                  data.district_code,
                                  (status, msg) => {
                                    this.setState({
                                      loading_assigning_data: false,
                                    });
                                  }
                                );
                              }}
                              close={() =>
                                this.setState({ select_district_open: false })
                              }
                              data={this.state.district_list}
                              id="district_code"
                              title="district_name"
                              loading={this.state.loading_districts}
                            />
                          </div>
                        ) : (
                          <div
                            className="w-full lg:w-1/2 py-1 px-3"
                            onClick={() =>
                              this.setState({ select_district_open: true })
                            }
                          >
                            <TextInput
                              disabled={true}
                              label="District"
                              name="district"
                              onChange={(e) => {}}
                              value={this.state.district_name}
                              error={
                                this.state.error.target ===
                                "district_list_select"
                                  ? this.state.error.msg
                                  : ""
                              }
                              placeholder={"Select a district"}
                              className="p-2"
                            />
                          </div>
                        )}
                      </>
                    )}

                {this.props.formAccess.school_code && (
                  <>
                    {this.props.school !== undefined ? (
                      <>
                        <div
                          className="w-full lg:w-1/2 py-1 px-3"
                          onClick={() =>
                            this.setState({
                              select_school_open: true,
                              select_position_open: false,
                            })
                          }
                        >
                          <TextInput
                            disabled={true}
                            label="School name"
                            name="school_name"
                            onChange={(e) => {}}
                            value={this.props.school.school_name}
                            error={
                              this.state.error.target === "school_code"
                                ? this.state.error.msg
                                : ""
                            }
                            placeholder={"Select school"}
                            className="p-2"
                          />
                        </div>
                      </>
                    ) : (
                      <>
                        {this.props.formAccess.school_code &&
                          this.props.basic.qualifications !== null && (
                            <>
                              {this.state.select_school_open === true ? (
                                <div className="w-full lg:w-1/2 py-1 px-3">
                                  <SelectCustom
                                    click={(data: any) => {
                                      this.setState({
                                        school_code: data.school_code,
                                        select_school_open: false,
                                        error: {
                                          target: null,
                                          msg: "",
                                        },
                                        position_id: "",
                                      });
                                      // add a check
                                      this.loadSchoolPosts(data.school_code);
                                    }}
                                    close={() =>
                                      this.setState({
                                        select_school_open: false,
                                      })
                                    }
                                    data={SchoolsAvailablesList}
                                    id="school_code"
                                    title="school_name"
                                    loading={
                                      this.state.loading_assigning_data ||
                                      this.state.loading
                                    }
                                  />
                                </div>
                              ) : (
                                <div
                                  className="w-full lg:w-1/2 py-1 px-3"
                                  onClick={() =>
                                    this.setState({ select_school_open: true })
                                  }
                                >
                                  <TextInput
                                    disabled={true}
                                    label="School name"
                                    name="school_name"
                                    onChange={(e) => {}}
                                    value={selected_school_name}
                                    error={
                                      this.state.error.target === "school_code"
                                        ? this.state.error.msg
                                        : ""
                                    }
                                    placeholder={"Select school"}
                                    className="p-2"
                                  />
                                </div>
                              )}
                            </>
                          )}
                      </>
                    )}
                  </>
                )}
              </div>

              {this.props.formAccess.stakeholder_id && (
                <div className="w-full lg:w-1/2 py-1 px-3">
                  <InputSelect
                    data={[
                      { title: "", value: "" },
                      ...assign_teacher_assets.stakeholders.map((itm) => ({
                        title: itm.stakeholder_name,
                        value: itm.stakeholder_id,
                      })),
                    ]}
                    error={
                      this.state.error.target === "stakeholder_id"
                        ? this.state.error.msg
                        : ""
                    }
                    value={this.state.stakeholder_id}
                    onChange={(e) =>
                      this.setState({
                        stakeholder_id: e.target.value,
                        error: { target: null, msg: "" },
                      })
                    }
                    label="Select a stakeholder"
                    name="select_a_stakeholder"
                    placeholder="Select a value"
                  />
                </div>
              )}

              {this.props.formAccess.academic_year_id &&
                this.props.basic.academic_calender !== null && (
                  <div className="w-full lg:w-1/2 py-1 px-3">
                    <InputSelect
                      data={[
                        {
                          title:
                            this.props.basic.academic_calender
                              ?.academic_year_name,
                          value:
                            this.props.basic.academic_calender
                              ?.academic_year_id,
                        },
                      ]}
                      error={
                        this.state.error.target === "academic_year_id"
                          ? this.state.error.msg
                          : ""
                      }
                      value={this.state.academic_year_id}
                      onChange={(e) =>
                        this.setState({
                          academic_year_id: e.target.value,
                          error: { target: null, msg: "" },
                        })
                      }
                      label="Academic year"
                      name="accademic_year"
                      placeholder="Select a value"
                    />
                  </div>
                )}

              {this.props.formAccess.role_id && (
                <>
                  {this.state.select_role_open === true ? (
                    <div className="w-full lg:w-1/2 py-1 px-3">
                      <SelectCustom
                        click={(data: any) => {
                          this.setState({
                            role_id: data.role_id,
                            select_role_open: false,
                            error: {
                              target: null,
                              msg: "",
                            },
                          });
                        }}
                        close={() => this.setState({ select_role_open: false })}
                        data={
                          this.props.exclude_roles !== undefined &&
                          this.props.exclude_roles.length >= 1
                            ? assign_teacher_assets.roles.filter(
                                (rol) =>
                                  !this.props.exclude_roles?.includes(
                                    rol.role_id
                                  )
                              )
                            : assign_teacher_assets.roles
                        }
                        id="role_id"
                        title="role"
                        loading={this.state.loading}
                      />
                    </div>
                  ) : (
                    <div
                      className="w-full lg:w-1/2 py-1 px-3"
                      onClick={() => this.setState({ select_role_open: true })}
                    >
                      <TextInput
                        disabled={true}
                        label="Role"
                        name="role"
                        onChange={(e) => {}}
                        value={role_name}
                        error={
                          this.state.error.target === "role_id"
                            ? this.state.error.msg
                            : ""
                        }
                        placeholder={"Select a role"}
                        className="p-2"
                      />
                    </div>
                  )}
                </>
              )}

              {this.props.formAccess.position_id &&
                this.props.position_code === undefined && (
                  <>
                    {this.state.select_position_open === true ? (
                      <div className="w-full lg:w-1/2 py-1 px-3">
                        <p>Position</p>
                        <SelectCustom
                          click={(data: any) => {
                            this.setState({
                              position_id: data.position_code,
                              select_position_open: false,
                              qualification_id: data.qualification_id,
                              error: {
                                target: null,
                                msg: "",
                              },
                            });
                          }}
                          close={() =>
                            this.setState({ select_position_open: false })
                          }
                          data={
                            this.props.only_post_for_school !== undefined
                              ? this.state.school_posts !== null
                                ? this.state.school_posts?.school_positions.map(
                                    (pos_s) => ({
                                      position_code: pos_s.position_code,
                                      position_name: `${pos_s.position_code} - ${pos_s.position_name} - ${pos_s.qualification_name}`,
                                      qualification_id: pos_s.qualification_id,
                                      qualification_name:
                                        pos_s.qualification_name,
                                    })
                                  )
                                : []
                              : assign_teacher_assets.positions.map((pos) => ({
                                  position_code: pos.position_code,
                                  position_name: `${pos.position_code} - ${pos.position_name} - ${pos.qualification_name}`,
                                  qualification_id: pos.qualification_id,
                                  qualification_name: pos.qualification_name,
                                }))
                          }
                          id="position_code"
                          title="position_name"
                          loading={this.state.loading}
                        />
                      </div>
                    ) : (
                      <div
                        className="w-full lg:w-1/2 py-1 px-3"
                        onClick={() => {
                          if (this.state.school_code === "") {
                            this.setState({
                              select_position_open: false,
                              select_school_open: true,
                              error: {
                                target: "school_code",
                                msg: "Please select the school",
                              },
                            });
                          } else {
                            this.setState({ select_position_open: true });
                          }
                        }}
                      >
                        <TextInput
                          disabled={true}
                          label="Position"
                          name="role"
                          onChange={(e) => {}}
                          value={positions_name}
                          error={
                            this.state.error.target === "position_id"
                              ? this.state.error.msg
                              : ""
                          }
                          placeholder={"Select a position"}
                          className="p-2"
                        />
                      </div>
                    )}
                  </>
                )}

              {this.props.qualification_id === undefined &&
                this.props.formAccess.qualification_id &&
                this.props.basic.qualifications !== null && (
                  <div className="w-full lg:w-1/2 py-1 px-3">
                    <InputSelect
                      data={[
                        {
                          title: "",
                          value: "",
                        },
                        ...this.props.basic.qualifications.map((itm) => ({
                          title: itm.qualification_name,
                          value: itm.qualification_id,
                        })),
                      ]}
                      error={
                        this.state.error.target === "qualification_id"
                          ? this.state.error.msg
                          : ""
                      }
                      value={this.state.qualification_id}
                      onChange={(e) => {
                        // this.setState({
                        //   qualification_id: e.target.value,
                        //   error: { target: null, msg: "" },
                        // })
                      }}
                      disabled
                      label="Qualification"
                      name="accademic_year"
                      placeholder="Select a value"
                    />
                  </div>
                )}

              {this.props.formAccess.sector_code && (
                <>
                  {this.state.select_sector_open === true ? (
                    <div className="w-full lg:w-1/2 py-1 px-3">
                      <SelectCustom
                        click={(data: any) => {
                          this.setState({
                            sector_code: data.sector_code,
                            select_sector_open: false,
                          });
                        }}
                        close={() =>
                          this.setState({ select_sector_open: false })
                        }
                        data={assign_teacher_assets.sectors.map((itm) => ({
                          ...itm,
                          sector_name: `${itm.sector_code} - ${itm.sector_name}`,
                        }))}
                        id="sector_code"
                        title="sector_name"
                        loading={this.state.loading}
                      />
                    </div>
                  ) : (
                    <div
                      className="w-full lg:w-1/2 py-1 px-3"
                      onClick={() =>
                        this.setState({ select_sector_open: true })
                      }
                    >
                      <TextInput
                        disabled={true}
                        label="Sector name"
                        name="sector_name"
                        onChange={(e) => {}}
                        value={sector_name}
                        error={
                          this.state.error.target === "sector_code"
                            ? this.state.error.msg
                            : ""
                        }
                        placeholder={"Select a sector"}
                        className="p-2"
                      />
                    </div>
                  )}
                </>
              )}

              {this.props.formAccess.start_date_in_the_school && (
                <div className="w-full lg:w-1/2 py-1 px-3">
                  <TextInput
                    disabled={this.state.loading}
                    label="Date of hire (Date in education)"
                    name="start_date_in_the_school"
                    type="date"
                    onChange={(e) =>
                      this.setState({
                        start_date_in_the_school: e.target.value,
                        error: { target: null, msg: "" },
                      })
                    }
                    value={this.state.start_date_in_the_school}
                    error={
                      this.state.error.target === "start_date_in_the_school"
                        ? this.state.error.msg
                        : ""
                    }
                    placeholder={"Select date"}
                    className="p-2"
                  />
                </div>
              )}

              {this.props.formAccess.country_id && (
                <div className="w-full lg:w-1/2 py-1 px-3">
                  <InputSelect
                    data={[
                      {
                        title: "No",
                        value: "",
                      },
                      {
                        title: "Yes",
                        value: "1",
                      },
                    ]}
                    error={
                      this.state.error.target === "country_id"
                        ? this.state.error.msg
                        : ""
                    }
                    value={this.state.country_id}
                    onChange={(e) =>
                      this.setState({
                        country_id: e.target.value,
                        error: { target: null, msg: "" },
                      })
                    }
                    label="User on country level"
                    name="country_level"
                    placeholder="Select a value"
                  />
                </div>
              )}
            </div>

            <div className="px-6 text-right flex">
              <div className="flex-1">
                {this.state.error.target !== null &&
                  this.state.error.target !== "main-error" &&
                  this.state.error.target !== "main-success" && (
                    <div className="text-sm text-red-400 pr-4">
                      Some fields are required! {"("}
                      {this.state.error.target}
                      {")"}
                    </div>
                  )}
              </div>
              <div className="">
                <Button type="submit" size="lg" loading={this.state.loading}>
                  {this.props.button_title === undefined
                    ? "Add new staff"
                    : this.props.button_title}
                </Button>
              </div>
            </div>
          </form>
        </DashboardContainer>
      </>
    );
  }
}

const mapStateToProps = ({
  auth,
  basic,
}: StoreState): { auth: Auth; basic: Basic } => {
  return { auth: auth, basic: basic };
};

export const AssignStuffComponent = connect(mapStateToProps, {
  FC_GetAssignAssets,
  FC_GetSchoolsByDistrictRedux,
})(_AssignStuffComponent);
